body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
    color: white;
    background-color: #282c34;
}

select {
    background-color: #282c34;
    color: white;
}
.player {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.player__notes {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: wrap;
    max-height: 100vh;
    align-content: space-between;
}

.player__middle {
    position: fixed;
    left: 33vw;
    right: 33vw;
    top: 0;
    bottom: 0;
    text-align: center;
    
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
}

.player__button {
    box-sizing: border-box;
    border-radius: 2vh;
    background-color: #ddd;
    display: inline-block;
    color: black;
    font-weight: bold;

    height: 20vh;
    width: 30vw;

    display: flex;
    justify-content: center;
    align-items: center;
}

.player__button:not(.player__button--top) {
    border-top: solid black 1px;
}

.player__button--left {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.player__button--right {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.player__button--highlight {
    background-color: #ffffff;
}

.player__button--octaveDown,
.player__button--octaveUp {
    padding: 0 3em;
}

.player__button--octaveDown {
    bottom: 8vh;
    margin-bottom: 10vh;
}

.player__button--octaveUp {
    bottom: 30vh;
}

.player__button--active {
    color: white;
    background-color: #aa99ff;
}

.player__back {
    margin-top: 1em;
    color: #aa99ff;
    text-align: center;
}

.player__key {
    font-weight: bold;
}

.player__spacer {
    flex-grow: 1;
}


@media (orientation: landscape) {
    .player__button--left,
    .player__button--right {
        width: 22vw;
    }

    .player__button--octaveDown,
    .player__button--octaveUp {
        width: 50vw;
    }    
}
.site {
    min-height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
}

.site__logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.site__header {
    font-weight: 450;
}

.site__help {
    text-align: left;
    padding: 0 1em;
}

.site__help li + li {
    margin-top: 0.5em;
}

.site__title {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    margin-top: 0;
    margin-bottom: 0.25em;
    text-transform: uppercase;
}

.site__header p {
    margin: 0;
}

.site__links {
    display: flex;
    justify-content: space-around;
    width: 16em;
    margin: 0.5em 0;
}

.site__link {
    color: #aa99ff;
}

.site__options {
    margin-top: 0.5em;
    display: flex;
    flex-direction: column;
}

.site__option {
    display: flex;
}

.site__label {
    text-align: left;
    margin-right: 0.5em;
}

.site__value {
    flex-grow: 1;
}

@media (orientation: portrait) {
    .site__option:not(:first-child) {
        margin-top: 0.5em;
    }
}

@media (orientation: landscape) {
    .site__options {
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 0.5em;
        grid-column-gap: 2em;
        grid-auto-flow: column;
    }
}


@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}


@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

